.footer {
  padding-top: 40px;
  padding-bottom: 50px;
  border-top: 1px solid rgba(238, 238, 238, 0.1);
  margin-top: auto;
}
.footer__container {
}
.footer__menu {
}
.footer__menu {
  margin-bottom: 28px;
}
.menu-footer {
}
.menu-footer__list {
  display: flex;
  column-gap: 48px;
  row-gap: 15px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.menu-footer__item {
}
.menu-footer__link {
  line-height: 133.333333%;
  padding-left: 34px;
}
.menu-footer__link:hover {
  text-decoration: underline;
}
.menu-footer__link_home {
  background: url('../assets/icons/home.svg') left no-repeat;
}
.menu-footer__link_about {
  background: url('../assets/icons/user.svg') left no-repeat;
}
.menu-footer__link_contacts {
  background: url('../assets/icons/phone.svg') left no-repeat;
}
.footer__social {
}
.footer__text {
  transition: all .3s ease-in-out;
}
.footer__text:hover {
  color: #00ADB5;
}